'use strict';

let _ = require('lodash');
require('echarts/chart/line');

var dataCount = 15;

module.exports = {
  title: {text: ''},
  tooltip: {show: true},
  legend: {data: ['']},
  xAxis: [{
    type: 'category',
    data: _.range(0, dataCount, 0).map(()=>"")
  }],
  yAxis: [{type: 'value'}],
  series: [{
    name: '',
    type: 'line',
    data: _.range(0, dataCount, 0)
  }]
};


